.movie {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 48%;
  margin-top: 70px;
  padding: 1.5em 1em 3em 1em;
  border-radius: 0.3em;
  background-color: #fff;
  color: #868e96;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); }
  .movie__poster {
    padding-right: 20px; }
    .movie__poster img {
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
      margin-top: -40px;
      max-width: 120px; }
  .movie__title {
    color: #111;
    font-size: 1.1rem; }
  .movie__year {
    margin-top: 0.3em; }
  .movie__genres {
    margin: 0.7em 0;
    padding: 0;
    display: flex;
    justify-content: flex-start;
    font-size: 0.7em; }
    .movie__genres .genre {
      padding-right: 0.5em;
      list-style: none; }
  .movie__summary {
    line-height: 1.3; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  background-color: #eff3f7;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; }

html,
body,
#root,
.container {
  height: 100%;
  display: flex;
  justify-content: center; }

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

.movies {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 50px;
  width: 90%; }

@media (max-width: 1024px) {
  .movies {
    width: 90%;
    padding: 0; }
    .movies .movie {
      width: 100%; } }

